import React from 'react'
import domtoimage from 'dom-to-image'

class MemGenerator extends React.Component {
    constructor() {
        super()
        this.state = {
            topText: 'enter top text here',
            bottomText: 'and bottom text here',
            randomImage: 'https://i.imgflip.com/1bij.jpg',
            allMemImages: [],
            fontSize: 60
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }

    componentDidMount() {
        fetch('https://api.imgflip.com/get_memes')
            .then(response => response.json())
            .then(response => {
                this.setState({
                    allMemImages: response.data.memes
                })
            })
    }

    handleChange(event) {
        let {name, value} = event.target

        this.setState({
            [name]: value
        })
    }

    handleSubmit(event) {
        event.preventDefault()
        let imgNumber = Math.floor(Math.random() * 100)
        this.setState({
            randomImage: this.state.allMemImages[imgNumber].url
        })
    }

    saveImage(event) {
        event.preventDefault()
        domtoimage.toJpeg(document.getElementById('my-node'), {quality: 0.95})
            .then(function (dataUrl) {
                var link = document.createElement('a');
                link.download = 'my-image-name.jpeg';
                link.href = dataUrl;
                link.click()
            });
    }

    render() {
        return (
            <div className="container">
                <div className="memContent" id="my-node">
                    <h2 style={{fontSize: this.state.fontSize+"px"}} className="topText">{this.state.topText}</h2>
                    <img src={this.state.randomImage} alt="mem_image"/>
                    <h2 style={{fontSize: this.state.fontSize+"px"}} className="bottomText">{this.state.bottomText}</h2>
                </div>
                <div className="form-group mb-0 mt-3">
                    <p className="d-inline">Font size in px:</p>
                    <input
                        className="ml-2"
                        name="fontSize"
                        type="number"
                        value={this.state.fontSize}
                        onChange={this.handleChange}
                    />
                </div>
                <form onSubmit={this.handleSubmit}>
                    <input
                        className="form-control mt-1"
                        type="text"
                        name="topText"
                        placeholder="Enter top text"
                        value={this.state.topText}
                        onChange={this.handleChange}
                    />
                    <input
                        className="form-control mt-1"
                        type="text"
                        name="bottomText"
                        placeholder="Enter bottom text"
                        value={this.state.bottomText}
                        onChange={this.handleChange}
                    />
                    <button className="btn-primary mt-2" onClick={this.handleChange}>Randomize image</button>
                    <br/>
                    <button className="btn-primary mt-2" onClick={this.saveImage}>Save image</button>
                </form>
            </div>
        )
    }
}

export default MemGenerator